import React, { useState,useEffect } from 'react'
import RCTable from 'rc-table';
import 'rc-pagination/assets/index.less';
import axios from 'axios'
import './table.less'
import 'rc-select/assets/index.less';
import IPagination from '../Pagination/IPagination'
export default function Table({title,columns,initialData =[],url,pagination}){
  const [data, setData] = useState(initialData)
  const [page, setPage] = useState(1)
  const [count, setCount] = useState()
  const [pageSize, setPageSize] = useState(10)

  const loadData = async (url,page,size = pageSize) => {
    let restUrl = url;
    if(pagination){
      restUrl = `${restUrl}&page=${page}&page_amount=${size}`
    }
    const res = await axios.get(restUrl);
    if(res.status === 200 && Array.isArray(res.data.data)){
      setCount(res.data.count)
      setData(res.data.data)
      setPage(page)
    }
  }

  const onPage = (page) => {
    loadData(url,page)
  }

  const onShowSizeChange = (current,pageSize) => {
    setPageSize(pageSize);
    loadData(url,current,pageSize)
  }

  useEffect(() => {
    if(url){
      loadData(url,1);
    } 
  }, [url])
  return(
    <div className='table'>
      <div className='t-title'>{title}</div>
      <RCTable data={data} columns={columns} footer={() => pagination 
        && <IPagination page={page} count={count} pageSize={pageSize} onPage = {onPage} onShowSizeChange={(current,pageSize) => onShowSizeChange(current,pageSize)}/> }/>
    </div>
  )
}
